.f-carousel {
    width: 100%;
    min-height: 100px;
}
.f-carousel .slick-slider {
    position: relative;
    display: block;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -ms-touch-action: pan-y;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}
.f-carousel .slick-list {
    position: relative;
    overflow: hidden;
    display: block;
    margin: 0;
    padding: 0;
}
.f-carousel .slick-list:focus {
    outline: none;
}
.f-carousel .slick-list.dragging {
    cursor: pointer;
    cursor: hand;
}
.f-carousel .slick-slider .slick-track,
.f-carousel .slick-slider .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
.f-carousel .slick-track {
    position: relative;
    left: 0;
    top: 0;
    display: block;
}
.f-carousel .slick-track:before,
.f-carousel .slick-track:after {
    content: "";
    display: table;
}
.f-carousel .slick-track:after {
    clear: both;
}
.slick-loading .f-carousel .slick-track {
    visibility: hidden;
}
.f-carousel .slick-slide {
    float: left;
    height: 100%;
    min-height: 1px;
    display: none;
}
[dir="rtl"] .f-carousel .slick-slide {
    float: right;
}
.f-carousel .slick-slide img {
    display: block;
}
.f-carousel .slick-slide.slick-loading img {
    display: none;
}
.f-carousel .slick-slide.dragging img {
    pointer-events: none;
}
.f-carousel .slick-initialized .slick-slide {
    display: block;
}
.f-carousel .slick-loading .slick-slide {
    visibility: hidden;
}
.f-carousel .slick-vertical .slick-slide {
    display: block;
    height: auto;
    border: 1px solid transparent;
}
.f-carousel .slick-arrow.slick-hidden {
    display: none;
}
.f-carousel .slick-prev,
.f-carousel .slick-next {
    position: absolute;
    display: block;
    height: 20px;
    width: 20px;
    line-height: 0;
    font-size: 0;
    cursor: pointer;
    background: transparent;
    color: transparent;
    top: 50%;
    margin-top: -10px;
    padding: 0;
    border: 0;
    outline: none;
}
.f-carousel .slick-prev:hover,
.f-carousel .slick-next:hover,
.f-carousel .slick-prev:focus,
.f-carousel .slick-next:focus {
    outline: none;
    background: transparent;
    color: transparent;
}
.f-carousel .slick-prev:hover:before,
.f-carousel .slick-next:hover:before,
.f-carousel .slick-prev:focus:before,
.f-carousel .slick-next:focus:before {
    opacity: 1;
}
.f-carousel .slick-prev.slick-disabled:before,
.f-carousel .slick-next.slick-disabled:before {
    opacity: 0.25;
}
.f-carousel .slick-prev {
    left: -25px;
}
.f-carousel .slick-prev:before {
    content: "";
}
.f-carousel .slick-next {
    right: -25px;
}
.f-carousel .slick-next:before {
    content: "";
}
.f-carousel .slick-dots {
    position: absolute;
    bottom: 4px;
    list-style: none;
    display: block;
    text-align: center;
    margin: 0;
    padding: 0;
    width: 100%;
}
.f-carousel .slick-dots li {
    position: relative;
    display: inline-block;
    height: 20px;
    width: 20px;
    margin: 0 5px;
    padding: 0;
    cursor: pointer;
}
.f-carousel .slick-dots li button {
    border: 0;
    background: transparent;
    display: block;
    height: 20px;
    width: 20px;
    outline: none;
    line-height: 0;
    font-size: 0;
    color: transparent;
    padding: 5px;
    cursor: pointer;
}
.f-carousel .slick-dots li button:hover,
.f-carousel .slick-dots li button:focus {
    outline: none;
}
.f-carousel .slick-dots li button:hover:before,
.f-carousel .slick-dots li button:focus:before {
    opacity: 1;
}
.f-carousel .slick-dots li button:before {
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    width: 20px;
    height: 20px;
    font-size: 28px;
    font-family: arial, sans-serif;
    line-height: 20px;
    text-align: center;
    color: gray;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
    opacity: 0.25;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.f-carousel .slick-dots li.slick-active button:before {
    color: #2DB7F5;
    opacity: 0.75;
}
.f-carousel-vertical .slick-slider {
    padding-bottom: 0;
}
.f-carousel-vertical .slick-dots {
    margin: 0;
    width: 20px;
    bottom: auto;
    right: -35px;
    top: 0;
}
.f-carousel-banner .slick-slider {
    padding-bottom: 0;
    background: #fff;
}
.f-carousel-banner .slick-dots li {
    margin: 0;
}
.f-carousel-banner .slick-slide h3 {
    background: #fff;
    color: #5B5B5B;
}
.f-carousel.f-carousel-card .slick-slider {
    height: 352px;
}
.f-carousel.f-carousel-card .slick-slide {
    background: #fff;
    height: 352px;
    line-height: 352px;
}
.f-carousel.f-carousel-card .slick-dots {
    bottom: -32px;
}
.f-carousel.f-carousel-card .slick-dots li {
    margin: 0 8px;
}
.slider img {
    width: 100%;
}


